import { parse } from "semver";

//
class dataTimeToFormat {
    timestampToTime(timestamp) {
        var date = new Date(timestamp * 1000);//10*1000131000
        var Y = date.getFullYear() + '-';
        var M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-';
        var D = (date.getDate() + 1 < 10 ? '0' + (date.getDate()) : date.getDate());
        var h = date.getHours() + ':';
        var m = date.getMinutes() + ':';
        var s = date.getSeconds();
        return Y + M + D; //+ h  + m + s
    }

    timestampToTimeSeparator(timestamp, separator = '-') {
        var date = new Date(timestamp * 1000);//10*1000131000
        var Y = date.getFullYear() + separator;
        var M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + separator;
        var D = (date.getDate() + 1 < 10 ? '0' + (date.getDate()) : date.getDate());
        var h = date.getHours() + ':';
        var m = date.getMinutes() + ':';
        var s = date.getSeconds();
        return Y + M + D; //+ h  + m + s
    }

    timestampToTimeDHM(timestamp) {
        var date = new Date(timestamp * 1000); //10*1000131000
        var Y = date.getFullYear() + "-";
        var M =
            (date.getMonth() + 1 < 10
                ? "0" + (date.getMonth() + 1)
                : date.getMonth() + 1) + "";
        var D = (date.getDate() + 1 < 10 ? "0" + date.getDate() : date.getDate())+ " ";
        var h = date.getHours() + ":";
        var m = date.getMinutes() < 10 ? "0" + date.getMinutes() : date.getMinutes();
        var s = date.getSeconds();
        return M + D + h + m; //+ h  + m + s
    }

    //
    sToHs(s) {
        //
        //60
        var h;
        h = Math.floor(s / 60);
        //
        //%60
        s = s % 60;
        //
        h += '';
        s += '';
        //0
        h = (h.length == 1) ? '0' + h : h;
        s = (s.length == 1) ? '0' + s : s;
        return h + ':' + s;
    }
    //    
    formatTime(time) {
        let dataNow = new Date()
        let timeDiff = dataNow.getTime() - time * 1000   //
        let days = Math.floor(timeDiff / (24 * 3600 * 1000));//
        let leave1 = timeDiff % (24 * 3600 * 1000);//
        let hours = Math.floor(leave1 / (3600 * 1000)) //
        let leavel2 = timeDiff % (3600 * 1000); //
        let minutes = Math.floor(leavel2 / (60 * 1000)); // 

        var date = new Date(time * 1000);//10*1000131000
        var M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1);
        var D = (date.getDate() + 1 < 10 ? '0' + (date.getDate()) : date.getDate());
        if (days !== 0 && days <= 2) {
            if (days == 1) {
                return ""
            } else if (days == 2) {
                return ""
            } 
        }else if(days >= 3){
            return M + "" + D + ""
        } else if (days == 0 && hours != 0) {
            return hours + ""
        } else {
            if (minutes >= 5 && minutes < 60) {
                return minutes + ""
            } else if (minutes < 5 && minutes >= 0) {
                return ""
            }
        }
    }
}
const dataTimeToFormats = new dataTimeToFormat()
export default dataTimeToFormats
