let LinePointShape = {

    NORMAL:1,             //
    ARROW:2,              //
    BAMBOO_ARROW:3,       //
    SIMPLE_ARROW:4,       //
    CIRCULAR:5,           //
    CIRCULAR_RADIATION:6, //
    CIRCULAR_HOLLOW:7,    //
    T_SHAPED:8,           //
    DIAMOND:9,           //


    from:function(value) {
        for(let mealName of Object.values(this)){
            if(mealName == value){
                return value;
            }
        }
        return this.Unknown;
    }

}

export default LinePointShape